import { FunctionComponent, VNode } from 'preact';
import {
  Modal,
  Button,
  ProgressSpinner,
} from '@adobe-commerce/elsie/components';
import '@/requisitionList/components/RequisitionListModal/RequisitionListModal.css';

export interface RequisitionListModalProps {
  isOpen: boolean;
  isLoading: boolean;
  title: VNode | string;
  modalContent: VNode | string;
  closeBtnCaption?: string;
  confirmBtnCaption?: string;
  handleModalOnClose?: () => void;
  handleModalOnConfirm?: () => void;
}

export const RequisitionListModal: FunctionComponent<
  RequisitionListModalProps
> = ({
  isOpen,
  isLoading,
  title,
  modalContent,
  closeBtnCaption,
  confirmBtnCaption,
  handleModalOnClose,
  handleModalOnConfirm,
}) => {
  if (!isOpen) return null;

  return (
    <Modal
      className="requisition-list-modal--overlay"
      data-testid="requisition-list-modal"
      size={'medium'}
      centered={false}
      title={title}
      onClose={handleModalOnClose}
      backgroundDim={true}
      clickToDismiss={true}
      escapeToDismiss={true}
      role="dialog"
      aria-label={title}
    >
      <div className="requisition-list-modal">
        {isLoading ? (
          <div
            className="requisition-list-modal__spinner"
            data-testid="progress-spinner"
          >
            <ProgressSpinner stroke={'4'} size={'large'} />
          </div>
        ) : null}
        <p>{modalContent}</p>
        <div className="requisition-list-modal__buttons">
          {handleModalOnClose && closeBtnCaption && (
            <Button
              data-testid="rl-modal-close-button"
              type={'button'}
              onClick={handleModalOnClose}
              variant="secondary"
              disabled={isLoading}
            >
              {closeBtnCaption}
            </Button>
          )}
          {handleModalOnConfirm && confirmBtnCaption && (
            <Button
              data-testid="rl-modal-confirm-button"
              type={'button'}
              onClick={handleModalOnConfirm}
              disabled={isLoading}
            >
              {confirmBtnCaption}
            </Button>
          )}
        </div>
      </div>
    </Modal>
  );
};
